:root {
  --font-poppins: "Poppins", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --yellow: hsl(49, 100%, 94%);
  --red: hsl(9, 82%, 57%);
  --blue: hsl(194, 100%, 39%);
  --text: hsl(214, 34%, 27%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  outline: none;
}

body {
  margin: 0;
  color: var(--text);
  font-family: var(--font-poppins);
  text-align: center;
}
@media (min-width: 71.875em) {
  body {
    text-align: left;
  }
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.2;
}

p {
  margin: 0;
}

input {
  color: var(--text);
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: var(--text);
}

button,
.button {
  color: var(--white);
  padding: 0.9375rem 0 1.125rem;
}

.container {
  padding: 0 1.25rem;
  max-width: 68.75rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.overlay {
  width: 100%;
  height: 100vh;
  position: absolute;
  background: linear-gradient(190deg, #fff9df 33%, white 100%);
}

.promo {
  position: relative;
  padding-top: 2rem;
  height: 100vh;
}
@media (min-width: 71.875em) {
  .promo {
    background: url("../images/background.svg") no-repeat 42vw 22vh/60%;
    padding-top: 4rem;
    overflow: hidden;
    z-index: 1;
  }
}

.header {
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 71.875em) {
  .header {
    flex-direction: row;
    margin-bottom: 5.25rem;
  }
}
.header__logo::before {
  content: "Where promises are kept";
  position: relative;
  display: block;
  height: 3.5625rem;
  width: 17.6875rem;
  text-align: center;
  color: var(--text);
  line-height: 3.25rem;
  margin-bottom: 1.25rem;
  background-image: url("../images/tagline.svg");
}
@media (min-width: 71.875em) {
  .header__logo::before {
    left: -3.75rem;
  }
}
.header__promise {
  margin-bottom: 1.25rem;
}
@media (min-width: 71.875em) {
  .header__promise {
    margin: 0;
  }
}
@media (min-width: 71.875em) {
  .header img {
    margin: 0;
  }
}
.header img:first-child {
  margin-bottom: 0.3125rem;
}
.header img:last-child {
  margin-bottom: 1.25rem;
}

.hero {
  display: flex;
  justify-content: space-between;
  flex-direction: column-reverse;
}
@media (min-width: 71.875em) {
  .hero {
    flex-direction: row;
  }
}
.hero__inputs {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3rem;
}
@media (min-width: 71.875em) {
  .hero__inputs {
    flex-direction: row;
    margin: 0;
  }
}
.hero__inputs > *:not(:last-child) {
  margin-bottom: 1.25rem;
}
.hero__text {
  margin-bottom: 3rem;
}
.hero__suptitle {
  font-size: 1.25rem;
}
.hero__title {
  font-size: 2.375rem;
  font-weight: 400;
}
.hero__input {
  position: relative;
  width: 10.4375rem;
  height: 2.375rem;
}
.hero__input input {
  width: 100%;
  padding-left: 2.125rem;
  background-color: transparent;
  border-width: 0;
}
.hero__input::before {
  content: "";
  display: block;
  position: absolute;
  width: 1.3125rem;
  height: 1.3125rem;
  background-repeat: no-repeat;
  background-position: 0 80%;
}
.hero__input::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 0.25rem;
  border-radius: 0.125rem;
}
.hero__input:first-child ::placeholder {
  color: var(--red);
}
.hero__input:first-child::before {
  background-image: url("../images/user.svg");
}
.hero__input:first-child::after {
  background-color: var(--red);
}
.hero__input:last-child ::placeholder {
  color: var(--blue);
}
.hero__input:last-child::before {
  background-image: url("../images/email.svg");
}
.hero__input:last-child::after {
  background-color: var(--blue);
}
.hero__textarea {
  width: 28.625rem;
  height: 8.375rem;
  background: url("../images/comment.svg") no-repeat 46% 44%;
  margin-bottom: 3rem;
  display: none;
}
@media (min-width: 71.875em) {
  .hero__textarea {
    display: block;
  }
}
.hero__textarea textarea {
  position: relative;
  bottom: -0.5rem;
  width: 26.125rem;
  height: 5.625rem;
  border-width: 0;
  color: var(--text);
  line-height: 1.6;
  font-size: 0.875rem;
  margin: 1.125rem 1.25rem;
  resize: none;
}
.hero__textarea ::placeholder {
  color: var(--text);
}
.hero__warning {
  margin-top: 3.125rem;
}
.hero__warning p {
  position: relative;
  padding: 4rem 0 0;
}
@media (min-width: 71.875em) {
  .hero__warning p {
    padding: 0 0 0 3.25rem;
  }
}
.hero__warning p::before {
  content: "";
  position: absolute;
  top: 0.5rem;
  left: 50%;
  transform: translate(-50%, 0);
  width: 2.125rem;
  height: 2.3125rem;
  background-image: url("../images/shield.svg");
}
@media (min-width: 71.875em) {
  .hero__warning p::before {
    left: 0;
  }
}
.hero button {
  background: linear-gradient(0deg, #eb5238 0%, #eb6f38 100%);
  padding: 0.875rem 3.5rem 0.875rem 1.5rem;
  display: block;
  margin: 0 auto 1.25rem;
  border-width: 0;
  border-radius: 1.25rem;
  cursor: pointer;
}
@media (min-width: 71.875em) {
  .hero button {
    margin-bottom: 0;
    margin-right: 1.875rem;
    display: inline-block;
  }
}
.hero a {
  text-decoration: underline;
}
.hero img {
  margin: 0 auto;
  max-width: 50%;
  margin-bottom: 3rem;
}
@media (min-width: 71.875em) {
  .hero img {
    position: relative;
    top: -12.1875rem;
    left: 9.6875rem;
    max-width: 100%;
  }
}